@import "./../default/default.scss";
.header-menu-container.container {
  .header-menu-container__buttons-block {
    display: none;
    @media (max-width: 700px) {
      display: flex;
      justify-content: space-between;
      padding: 12px 10px;
      padding-bottom: 14px;
      .interface__user {
        display: none;
        display: flex;
        gap: 27px;
        .user__login {
          display: flex;
          gap: 10px;
          align-items: baseline;
          font-family: "Raleway";
          font-style: normal;
          font-weight: 500;
          font-size: 14px;
          line-height: 16px;
          letter-spacing: 0.02em;
          &:hover {
            .user__login-text {
              color: $darkGreen;
            }
            .icon-user {
              color: $darkGreen;
            }
          }
          .icon-user {
            color: $green;
            font-size: 13px;
          }
          &-text {
            font-weight: 500;
            font-size: 13px;
            line-height: 15px;
            letter-spacing: 0.02em;
            color: #323232;
          }
        }
        .user__registration {
          box-sizing: border-box;
          font-family: "Raleway";
          font-style: normal;
          font-weight: 500;
          font-size: 14px;
          line-height: 16px;
          letter-spacing: 0.02em;
          color: #323232;
          padding-bottom: 2px;
          border-bottom: 1px solid $green;
          &:hover {
            color: $darkGreen;
          }
        }
      }
    }
    .social {
      display: flex;
      &__facebook {
        font-size: 15px;
        margin-bottom: 3px;
        margin-right: 2px;
      }
      &__instagram {
        font-size: 15px;
        margin-bottom: 3px;
      }
      &__youtube {
        font-size: 14px;
      }
    }
  }
  @media (max-width: 960px) {
    padding-left: 0;
    padding-right: 0;
    position: fixed;
    left: 0;
    top: 0;
    width: 300px;
    height: 100vh;
    background: $white;
    z-index: 3;
    transition: transform 0.3s ease-in-out;
    transform: translateX(-100%);
    display: none;
    &.active {
      left: 0;
      display: block;
      transition: transform 0.3s ease-in-out;
      transform: translateX(0%);
      border-right: 1px solid rgba($color: $gray, $alpha: 0.2);
    }
  }
  @media (max-width: 450px) {
    width: 100vw;
    height: max-content;
    border-bottom: 1px solid rgba($color: $gray, $alpha: 0.2);
    transform: translateY(-100%);
    &.active {
      transform: translateY(0%);
    }
  }
  .burger-icon-block {
    display: none;
    @media (max-width: $tablet) {
      padding: 13px 10px 14px 10px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .logo {
        padding: 0;
        gap: 9px;
        .logo-img {
          max-width: 34px;
          max-height: 33px;
        }
        flex-direction: row;
        .logo-text {
          text-align: start;
          font-size: 12px;
          line-height: 14px;
          letter-spacing: 0.02em;
        }
      }
      .burger-exit {
        @media (min-width: 961px) {
          display: none !important;
          width: fit-content;
          height: fit-content;
        }
        @media (max-width: 960px) {
          display: block;
          width: 16px;
          height: 16px;
          z-index: 5;
          cursor: pointer;
        }
      }
    }
  }
  .header-menu-container__menu {
    display: flex;
    gap: 55.5px;
    justify-content: center;
    font-family: "Raleway";
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
    letter-spacing: 0.02em;
    @media (max-width: 1200px) {
      gap: 30px;
    }
    @media (max-width: 1076px) {
      gap: 10px;
    }
    @media (max-width: 973px) {
      gap: 7px;
    }
    @media (max-width: $tablet) {
      justify-content: start;
      flex-direction: column;
      align-items: flex-start;
      padding-bottom: 3px;
      gap: 0;
    }
    .menu__item {
      text-align: center;
      margin-top: 22px;
      padding-left: 7px;
      padding-right: 6.5px;
      padding-bottom: 12px;
      font-family: "Raleway";
      font-style: normal;
      font-weight: 500;
      font-size: 15px;
      line-height: 18px;
      letter-spacing: 0.02em;
      border-bottom: 3px solid  rgba($color: white, $alpha: .0);
      &.active {
        color: $darkGreen;
        border-bottom: 3px solid $darkGreen;
      }
      cursor: pointer;
      @media (min-width: 961px) {
        &:hover {
          color: $darkGreen;
        }
        
      }
      @media (max-width: $tablet) {
        width: 100%;
        margin-top: 0;
        padding-top: 14px;
        padding-bottom: 10px;
        padding-left: 13px;
        text-align: start;

        &.active {
          background: $darkGreen;
          color: $white;
          margin-bottom: 1px;
          padding-top: 14px;
          padding-bottom: 14px;
          font-family: "Raleway";
          font-style: normal;
          font-weight: 700;
          font-size: 15px;
          line-height: 18px;
          letter-spacing: 0.02em;
        }
        &:hover {
          &:not(.active) {
            color: $darkGreen;
          }
        }
      }
    }
  }
}
