.custom-select {
  position: relative;
  font-family: "Montserrat";
}
.custom-select select {
  display: none;
}
.custom-select .select-selected {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 3px;
}
.custom-select .select-selected:hover {
  background: rgba(51, 125, 90, 0.1);
}
.custom-select .select-selected:after {
  font-family: icomoon;
  color: #93b474;
  font-size: 6px;
  content: "\e922";
  transform: rotate(180deg);
}
.custom-select .select-selected.select-arrow-active {
  border: 1.5px solid #337d5a;
}
.custom-select .select-selected.select-arrow-active:after {
  border-color: transparent transparent #93b474 transparent;
  transform: rotate(0deg);
}
.custom-select .select-items div,
.custom-select .select-selected {
  color: #1a2f3f;
  padding: 3px 0px 3px 5px;
  border: 1px solid transparent;
  border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
  cursor: pointer;
  user-select: none;
  border: 0.5px solid rgba(0, 0, 0, 0.15);
}
.custom-select .select-items {
  position: absolute;
  background-color: #ffffff;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 99;
}
.custom-select .select-hide {
  display: none;
}
.custom-select .select-items .same-as-selected {
  background-color: #337d5a;
  color: #ffffff;
}
.custom-select .select-items div:hover,
.custom-select .same-as-selected {
  background-color: #337d5a;
  color: #ffffff;
}

.header-menu-container.container .header-menu-container__buttons-block {
  display: none;
}
@media (max-width: 700px) {
  .header-menu-container.container .header-menu-container__buttons-block {
    display: flex;
    justify-content: space-between;
    padding: 12px 10px;
    padding-bottom: 14px;
  }
  .header-menu-container.container .header-menu-container__buttons-block .interface__user {
    display: none;
    display: flex;
    gap: 27px;
  }
  .header-menu-container.container .header-menu-container__buttons-block .interface__user .user__login {
    display: flex;
    gap: 10px;
    align-items: baseline;
    font-family: "Raleway";
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.02em;
  }
  .header-menu-container.container .header-menu-container__buttons-block .interface__user .user__login:hover .user__login-text {
    color: #337d5a;
  }
  .header-menu-container.container .header-menu-container__buttons-block .interface__user .user__login:hover .icon-user {
    color: #337d5a;
  }
  .header-menu-container.container .header-menu-container__buttons-block .interface__user .user__login .icon-user {
    color: #93b474;
    font-size: 13px;
  }
  .header-menu-container.container .header-menu-container__buttons-block .interface__user .user__login-text {
    font-weight: 500;
    font-size: 13px;
    line-height: 15px;
    letter-spacing: 0.02em;
    color: #323232;
  }
  .header-menu-container.container .header-menu-container__buttons-block .interface__user .user__registration {
    box-sizing: border-box;
    font-family: "Raleway";
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.02em;
    color: #323232;
    padding-bottom: 2px;
    border-bottom: 1px solid #93b474;
  }
  .header-menu-container.container .header-menu-container__buttons-block .interface__user .user__registration:hover {
    color: #337d5a;
  }
}
.header-menu-container.container .header-menu-container__buttons-block .social {
  display: flex;
}
.header-menu-container.container .header-menu-container__buttons-block .social__facebook {
  font-size: 15px;
  margin-bottom: 3px;
  margin-right: 2px;
}
.header-menu-container.container .header-menu-container__buttons-block .social__instagram {
  font-size: 15px;
  margin-bottom: 3px;
}
.header-menu-container.container .header-menu-container__buttons-block .social__youtube {
  font-size: 14px;
}
@media (max-width: 960px) {
  .header-menu-container.container {
    padding-left: 0;
    padding-right: 0;
    position: fixed;
    left: 0;
    top: 0;
    width: 300px;
    height: 100vh;
    background: #ffffff;
    z-index: 3;
    transition: transform 0.3s ease-in-out;
    transform: translateX(-100%);
    display: none;
  }
  .header-menu-container.container.active {
    left: 0;
    display: block;
    transition: transform 0.3s ease-in-out;
    transform: translateX(0%);
    border-right: 1px solid rgba(89, 89, 89, 0.2);
  }
}
@media (max-width: 450px) {
  .header-menu-container.container {
    width: 100vw;
    height: max-content;
    border-bottom: 1px solid rgba(89, 89, 89, 0.2);
    transform: translateY(-100%);
  }
  .header-menu-container.container.active {
    transform: translateY(0%);
  }
}
.header-menu-container.container .burger-icon-block {
  display: none;
}
@media (max-width: 960px) {
  .header-menu-container.container .burger-icon-block {
    padding: 13px 10px 14px 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .header-menu-container.container .burger-icon-block .logo {
    padding: 0;
    gap: 9px;
    flex-direction: row;
  }
  .header-menu-container.container .burger-icon-block .logo .logo-img {
    max-width: 34px;
    max-height: 33px;
  }
  .header-menu-container.container .burger-icon-block .logo .logo-text {
    text-align: start;
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 0.02em;
  }
}
@media (max-width: 960px) and (min-width: 961px) {
  .header-menu-container.container .burger-icon-block .burger-exit {
    display: none !important;
    width: fit-content;
    height: fit-content;
  }
}
@media (max-width: 960px) and (max-width: 960px) {
  .header-menu-container.container .burger-icon-block .burger-exit {
    display: block;
    width: 16px;
    height: 16px;
    z-index: 5;
    cursor: pointer;
  }
}
.header-menu-container.container .header-menu-container__menu {
  display: flex;
  gap: 55.5px;
  justify-content: center;
  font-family: "Raleway";
  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
  letter-spacing: 0.02em;
}
@media (max-width: 1200px) {
  .header-menu-container.container .header-menu-container__menu {
    gap: 30px;
  }
}
@media (max-width: 1076px) {
  .header-menu-container.container .header-menu-container__menu {
    gap: 10px;
  }
}
@media (max-width: 973px) {
  .header-menu-container.container .header-menu-container__menu {
    gap: 7px;
  }
}
@media (max-width: 960px) {
  .header-menu-container.container .header-menu-container__menu {
    justify-content: start;
    flex-direction: column;
    align-items: flex-start;
    padding-bottom: 3px;
    gap: 0;
  }
}
.header-menu-container.container .header-menu-container__menu .menu__item {
  text-align: center;
  margin-top: 22px;
  padding-left: 7px;
  padding-right: 6.5px;
  padding-bottom: 12px;
  font-family: "Raleway";
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
  letter-spacing: 0.02em;
  border-bottom: 3px solid rgba(255, 255, 255, 0);
  cursor: pointer;
}
.header-menu-container.container .header-menu-container__menu .menu__item.active {
  color: #337d5a;
  border-bottom: 3px solid #337d5a;
}
@media (min-width: 961px) {
  .header-menu-container.container .header-menu-container__menu .menu__item:hover {
    color: #337d5a;
  }
}
@media (max-width: 960px) {
  .header-menu-container.container .header-menu-container__menu .menu__item {
    width: 100%;
    margin-top: 0;
    padding-top: 14px;
    padding-bottom: 10px;
    padding-left: 13px;
    text-align: start;
  }
  .header-menu-container.container .header-menu-container__menu .menu__item.active {
    background: #337d5a;
    color: #ffffff;
    margin-bottom: 1px;
    padding-top: 14px;
    padding-bottom: 14px;
    font-family: "Raleway";
    font-style: normal;
    font-weight: 700;
    font-size: 15px;
    line-height: 18px;
    letter-spacing: 0.02em;
  }
  .header-menu-container.container .header-menu-container__menu .menu__item:hover:not(.active) {
    color: #337d5a;
  }
}